.App {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.header .title {
  font-size: 40px;
  text-shadow: 1px 1px 2px #315f81;
  text-transform: uppercase;
  padding-inline: 20px;
  border-bottom: 2px solid #315f81;
  text-align: center;
}
.weatherToday {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 0.5px;
  margin: 0;
  text-align: center;
}
.weatherToday .temperature {
  margin-bottom: 0;
  font-size: 35px;
  text-shadow: 1px 1px 2px #315f81;
}
.weatherToday .temperature p {
  margin-top: 0px;
}
.weatherToday .temperature span {
  color: #315f81;
  text-shadow: 1px 1px 2px #ffffff;
}
.weatherToday .title {
  font-size: 35px;
  text-transform: uppercase;
  text-shadow: 1px 1px 2px #315f81;
}
.weatherToday .icon {
  min-width: 90px;
  min-height: 90px;
}
.forecast {
  display: flex;
  flex-direction: row;
  max-width: 100%;
  overflow-x: auto;
}
.forecast::-webkit-scrollbar {
  display: none;
}
.forecastItem {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-inline: 15px;
  gap: 0.2px;
  text-align: center;
}
.forecastItem p {
  text-shadow: 1px 1px 2px #315f81;
}
@font-face {
  font-family: 'Noto Serif';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url(https://fonts.gstatic.com/s/notoserif/v21/ga6Iaw1J5X9T9RW6j9bNTFA.ttf) format('truetype');
}
body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100vh;
  background-color: #b8cee5;
  color: #ffffff;
  font-family: 'Noto Serif', serif;
}
